import { Tab, Tabs } from "react-bootstrap";
import ErrorFound from "../../../../../ErrorFound";
import GalleryTab from "./GalleryTab";
import WebsiteTab from "./WebsiteTab";
import ShopOnTab from "./ShopOnTab";
import { Card } from "react-bootstrap";
import { memo } from "react";

const ChannelPerfomance = memo((props) => {
    const {startDate, endDate} = props
    return <Card className={`gutter-b`}>
            <Card.Body>
                <div className="h4">Channel-Specific Performance</div>
                <Tabs defaultActiveKey="contentGallery" variant="underline" className="border-bottom">
                    <Tab eventKey="contentGallery" title="Content Gallery"> <ErrorFound><GalleryTab start={startDate} end={endDate}/></ErrorFound> </Tab>
                    <Tab eventKey="website" title="Website"> <ErrorFound><WebsiteTab start={startDate} end={endDate} /></ErrorFound> </Tab>
                    <Tab eventKey="shoponbio" title="Shopon.bio"> <ErrorFound><ShopOnTab start={startDate} end={endDate} /></ErrorFound> </Tab>
                </Tabs>
            </Card.Body>
        </Card>
})

export default ChannelPerfomance