import React, { Component } from 'react';
import { connect } from 'react-redux';
import { DASHBOARD_WEBSITE_ANALYTICS } from '../../../../../../actions/sceneActions';
import { CHANGE_WEBSITE, CHANGE_WEBSITE_WALL } from '../../../../../../actions/websiteAction';
import { ACTIVE_WEBSITE_ID_REDUCER } from '../../../../../../reducer/keys';
import moment from 'moment';
import withRouter from '../../../../../routes/WithRouter';
import { kFormatter } from '../../../../../../utils';
import { Button, Dropdown, DropdownButton, OverlayTrigger, Tooltip, Card, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';

class WebsiteTab extends Component {
    state = { activeGallery: [] }

    componentDidMount() {
        const { websiteData } = this.props
        if (localStorage.getItem('website_id')) {
            let gallery = websiteData?.websiteList.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id'));
            this.setState({ activeGallery: gallery })
        }
        else {
            let firstWallId = websiteData?.websiteList && websiteData?.websiteList.length ? websiteData?.websiteList[0] : ''
            this.setState({ activeGallery: firstWallId })
        }
    }

    componentDidUpdate(prevProps, prevState) {
        const { websiteData } = this.props
        if (websiteData?.websiteList !== prevProps.websiteData?.websiteList) {
            if (localStorage.getItem('website_id')) {
                let gallery = websiteData?.websiteList.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id'));
                this.setState({ activeGallery: gallery })
            }
            else {
                let firstWallId = websiteData?.websiteList && websiteData?.websiteList.length ? websiteData?.websiteList[0] : ''
                this.setState({ activeGallery: firstWallId })
            }
        }
    }

    viewDetails = (id) => {
        const { websiteData, getWebData, activeId, changeWall } = this.props
        localStorage.setItem('website_id', id);
        let data = websiteData?.websiteList?.find((x) => x.wall.Wall.id == id);
        getWebData(data);
        activeId();
        changeWall(data?.wall?.Wall?.wallId ? data?.wall?.Wall?.wallId : localStorage.getItem('active_wall'), 20, data?.wall?.ThemeRule?.themeId);
        setTimeout(() => {
            this.props.navigate(`/website/publish/${id}`)
        }, 100);
    }

    changeGallery = (e) => {
        const { websiteData, start, end, websiteAnalytics } = this.props
        let startDate = moment(start, "DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        var gallery = websiteData?.websiteList.find((item) => item.wall?.Wall?.id == e.id);
        this.setState({ activeGallery: gallery })
        websiteAnalytics(e.id, startDate, endDate)
    }

    render() {
        const { activeGallery } = this.state
        const { websiteData, website } = this.props
        return <>

            <div className="d-flex align-items-center justify-content-end py-3">
                {websiteData && websiteData.websiteList.length > 0 ? <><Button variant='link' size="sm" onClick={() => this.viewDetails(activeGallery?.wall?.Wall?.id)}>View Details</Button>
                    <DropdownButton size="sm" className='ms-3' title={activeGallery?.wall?.Wall?.name ? activeGallery?.wall?.Wall?.name : 'Loading...'}>
                        {websiteData?.websiteList?.map((item, i) => {
                            return <Dropdown.Item key={i} onClick={() => this.changeGallery(item.wall?.Wall)}>{item.wall?.Wall?.name}</Dropdown.Item>
                        })}
                    </DropdownButton></> : null}
            </div>
            <Row>
                {website ? website.series.map((Item, Index) => {
                    return <Col key={Index}>
                        <Card className={`card-stretch gutter-b shadow-none border`}>
                            <Card.Body>
                                <div className="f-between">
                                    <p className="fs-8 fw-medium text-muted mb-2">
                                        {Item.name}
                                    </p>
                                    <OverlayTrigger placement='top' overlay={<Tooltip>{Item.tool_tip}</Tooltip>} >
                                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
                                    </OverlayTrigger>
                                </div>
                                <div className="d-flex align-items-center flex-wrap">
                                    {Item?.networks && Item?.networks.length > 0 ? Item.networks.map((network, Index) => {
                                        return <div className='me-2 mt-2' key={Index}> <img src={network} height={24} width={24} alt="social-icon" /> </div>
                                    })
                                        :
                                        <p className="h1 mb-0">{kFormatter((Item.total))}</p>
                                    }
                                </div>
                            </Card.Body>
                        </Card>
                    </Col>
                })
                    : null}
            </Row>
        </>
    }
}

const mapStateToProps = state => {
    return {
        website: state.dashboardData.website,
        websiteData: state.websiteList,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        websiteAnalytics: (id, startDate, endDate) => dispatch(DASHBOARD_WEBSITE_ANALYTICS(id, startDate, endDate)),
        getWebData: (data) => dispatch(CHANGE_WEBSITE(data)),
        activeId: () => dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') }),
        changeWall: (wallId, limit, themeId) => dispatch(CHANGE_WEBSITE_WALL(wallId, limit, themeId)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(WebsiteTab))