import React, { Component } from 'react';
import { connect } from 'react-redux';
import withRouter from '../../../../../routes/WithRouter';
import { DASHBOARD_SHOPON_ANALYTICS } from '../../../../../../actions/sceneActions';
import { CHANGE_SHOPON_WALL } from '../../../../../../actions/shoponAction';
import moment from 'moment';
import { kFormatter } from '../../../../../../utils';
import { Button, Dropdown, DropdownButton, OverlayTrigger, Tooltip, Card, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';

class ShopOnTab extends Component {
    state = { activeShopOn: [] }

    componentDidMount() {
        const { shopOnData } = this.props
        if (localStorage.getItem('shopOn_id')) {
            let gallery = shopOnData?.shopOnList.find((item) => item.id == localStorage.getItem('shopOn_id'));
            this.setState({ activeShopOn: gallery })
        }
        else {
            let firstWallId = shopOnData?.shopOnList && shopOnData?.shopOnList.length ? shopOnData?.shopOnList[0] : ''
            this.setState({ activeShopOn: firstWallId })
        }
    }

    componentDidUpdate(prevProps, prevState) {
        const { shopOnData } = this.props
        if (shopOnData?.shopOnList !== prevProps.shopOnData?.shopOnList) {
            if (localStorage.getItem('shopOn_id')) {
                let gallery = shopOnData?.shopOnList.find((item) => item.id == localStorage.getItem('shopOn_id'));
                this.setState({ activeShopOn: gallery })
            }
            else {
                let firstWallId = shopOnData?.shopOnList && shopOnData?.shopOnList.length ? shopOnData?.shopOnList[0] : ''
                this.setState({ activeShopOn: firstWallId })
            }
        }
    }

    viewDetails = (id) => {
        const { shopOnData, allWalls } = this.props
            localStorage.setItem('shopOn_id', id);
            let webData = shopOnData?.shopOnList?.find((x) => x.id == id);
            let newWallId = webData?.wallId ? webData?.wallId : allWalls[0].id;
            localStorage.setItem('shopOnActive_wall', newWallId)
            setTimeout(() => {
                this.props.navigate(`/shopon-bio/preview/${id}`)
            }, 100);
    }

    ChangeGallery = (e) => {
        const { shopOnData, start, end, shopOnAnalytics } = this.props
        let startDate = moment(start, "DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        let gallery = shopOnData?.shopOnList.find((item) => item.id == e.id);
        this.setState({ activeShopOn: gallery })
        shopOnAnalytics(e.id, startDate, endDate)
    }

    render() {
        const { shopOnData, shopOn } = this.props
        const { activeShopOn } = this.state
        return <>
            <div className="d-flex align-items-center justify-content-end py-3">
                {shopOnData && shopOnData.shopOnList.length > 0 ? 
                    <><Button variant='link' size="sm" onClick={() => this.viewDetails(activeShopOn?.id)}>View Details</Button>
                    <DropdownButton size="sm" className='ms-3' title={activeShopOn?.name ? activeShopOn?.name : 'Loading...'}>
                        {shopOnData?.shopOnList?.map((item, i) => {
                            return <Dropdown.Item key={i} onClick={() => this.ChangeGallery(item)}>{item?.name}</Dropdown.Item>
                        })
                        }
                    </DropdownButton></>
                : null}
            </div>
            <Row>
                {shopOn ? shopOn.series.map((Item, Index) => {
                    return <Col key={Index}>
                        <Card className={`card-stretch gutter-b shadow-none border`}>
                            <Card.Body>
                                <div className="f-between">
                                    <p className="fs-8 fw-medium text-muted mb-2">
                                        {Item.name}
                                    </p>
                                    <OverlayTrigger placement='top' overlay={<Tooltip>{Item.tool_tip}</Tooltip>} >
                                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                    </OverlayTrigger>
                                </div>
                                <div className="d-flex align-items-center flex-wrap">
                                    <p className="h1 mb-0">{kFormatter((Item.total))}</p>
                                </div>
                            </Card.Body>
                        </Card>
                    </Col>
                }) : null}
            </Row>
        </>
    }
}

const mapStateToProps = state => {
    return {
        shopOn: state.dashboardData.shopon,
        shopOnData: state.shopOnList,
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        shopOnAnalytics: (id, startDate, endDate) => dispatch(DASHBOARD_SHOPON_ANALYTICS(id, startDate, endDate)),
        changeWall: (wallId, limit) => dispatch(CHANGE_SHOPON_WALL(wallId, limit)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ShopOnTab))