import React, { Component } from 'react'
import { connect } from 'react-redux';
import { DASHBOARD_GALLERY_ANALYTICS } from '../../../../../../actions/sceneActions';
import withRouter from '../../../../../routes/WithRouter';
import moment from 'moment';
import { GET_NETWORK_ICON } from '../../../../../../utils';
import { Button, Dropdown, DropdownButton, OverlayTrigger, Tooltip, Card, Row, Col, Image } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';

class GalleryTab extends Component {

    state = { activeGallery: [] }

    componentDidMount() {
        const { allWalls } = this.props
        if (localStorage.getItem('wallId')) {
            let gallery = allWalls?.find((item) => item.id == localStorage.getItem('wallId'));
            this.setState({ activeGallery: gallery })
        }
        else {
            let firstWallId = allWalls && allWalls.length ? allWalls[0] : ''
            this.setState({ activeGallery: firstWallId })
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.allWalls != nextProps.allWalls) {
            if (nextProps.allWalls && nextProps.allWalls.length > 0) {
                let activeWall = nextProps.allWalls?.find((item) => item.id == localStorage.getItem('wallId'));
                this.setState({ activeGallery: activeWall })
            }
        }
    }

    ChangeGallery = (e) => {
        const { allWalls, start, end, galleryAnalytics } = this.props
        let startDate = moment(start, "DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        let gallery = allWalls?.find((item) => item.id == e.id);
        this.setState({ activeGallery: gallery })
        galleryAnalytics(e.id, startDate, endDate)
    }

    viewDetails = (id) => {
        localStorage.setItem('wallId', id)
        this.props.navigate(`/content/moderation/${id}`)
    }

    render() {
        const { activeGallery } = this.state
        const { gallery, allWalls } = this.props
        return <>
            <div className="d-flex align-items-center justify-content-end  py-3">
                {allWalls && allWalls.length > 0 ? <><Button variant='link' size="sm" onClick={() => this.viewDetails(activeGallery?.id)}>
                    View Details
                </Button>
                <DropdownButton size="sm" className='ms-3' title={activeGallery?.name ? activeGallery.name : 'Loading...'}>
                    {allWalls?.map((item, i) => { return <Dropdown.Item key={i} onClick={() => this.ChangeGallery(item)}>{item?.name}</Dropdown.Item> })}
                </DropdownButton></> : null}
            </div>
            <Row>
                {gallery ? gallery.map((Item, Index) => {
                    return <Col key={Index}>
                        <Card className={`card-stretch gutter-b shadow-none border`}>
                            <Card.Body>
                                <div className="f-between">
                                    <p className="fs-8 fw-medium text-muted mb-2">
                                        {Item.label}
                                    </p>
                                    <OverlayTrigger placement='top' overlay={<Tooltip>{Item.tool_tip}</Tooltip>} >
                                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                    </OverlayTrigger>
                                </div>
                                <div className="d-flex align-items-center flex-wrap">
                                    {Item?.networks && Item?.networks.length > 0 ? Item.networks.map((network, Index) => {
                                        return <div className='me-2 mt-2' key={Index}> 
                                        <Image src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(network)}.svg`)} height={24} width={24} fluid />
                                        </div>
                                    })
                                        :
                                        <p className="h1 mb-0">{Item?.total}</p>
                                    }
                                </div>
                            </Card.Body>
                        </Card>
                    </Col>
                })
                    : null}
            </Row>
        </>
    }
}

const mapStateToProps = state => {
    return {
        gallery: state.dashboardData.content_gallery,
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        galleryAnalytics: (id, startDate, endDate) => dispatch(DASHBOARD_GALLERY_ANALYTICS(id, startDate, endDate)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(React.memo(GalleryTab)))